import React from 'react'
import './OnOffSwitch.css'
import { useState } from 'react'

function OnOffSwitch() {

  const [background, setBackground]=useState('night.jpg')
  const [color, setColor] = React.useState('#808fc7')

  const checkHandle = (event) => {
    setColor(event.target.checked ? '#C2E9F6'
                  :'#808fc7')
    setBackground(event.target.checked ? 'day.jpg':'night.jpg')
  }
  return (
    <div className="switch__container" style={{background:`${color}`}}>
      <div className="switch">
        <input type="checkbox" onChange={checkHandle} />
        <div
          className="slider"
          style={{ backgroundImage: `url(/assets/images/${background})` }}
        ></div>
      </div>
    </div>
  )
}

export default OnOffSwitch
